import React from 'react'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'
import Banner from '../components/Banner'

import pic01 from '../assets/images/pic01.jpg'
import pic02 from '../assets/images/pic02.jpg'
import pic03 from '../assets/images/pic03.jpg'
import pic04 from '../assets/images/pic04.jpg'
import pic05 from '../assets/images/pic05.jpg'
import pic06 from '../assets/images/pic06.jpg'

class HomeIndex extends React.Component {
    render() {
        const siteTitle = this.props.data.site.siteMetadata.title
        const siteDescription = this.props.data.site.siteMetadata.description

        return (
            <div>
                <Helmet>
                    <title>{siteTitle}</title>
                    <meta name="description" content={siteDescription} />
                </Helmet>

                <Banner />

                <div id="main">
                    <section id="one" className="tiles">
                        <article style={{backgroundImage: `url(${pic01})`}}>
                            <header className="major">
                                <h3>New Here?</h3>
                                <p>learn more about FFC</p>
                            </header>
                            <Link to="/newhere" className="link primary"></Link>
                        </article>
                        <article style={{backgroundImage: `url(${pic02})`}}>
                            <header className="major">
                                <h3>Listen</h3>
                                <p>hear our latest sermons</p>
                            </header>
                            <Link to="/landing" className="link primary"></Link>
                        </article>
                        <article style={{backgroundImage: `url(${pic03})`}}>
                            <header className="major">
                                <h3>Events</h3>
                                <p>what's happening @ FFC?</p>
                            </header>
                            <Link to="/landing" className="link primary"></Link>
                        </article>
                        <article style={{backgroundImage: `url(${pic04})`}}>
                            <header className="major">
                                <h3>Ministry</h3>
                                <p>serving God, serving people</p>
                            </header>
                            <Link to="/landing" className="link primary"></Link>
                        </article>
                        <article style={{backgroundImage: `url(${pic05})`}}>
                            <header className="major">
                                <h3>Contact Us</h3>
                                <p>got questions?</p>
                            </header>
                            <Link to="/landing" className="link primary"></Link>
                        </article>
                        <article style={{backgroundImage: `url(${pic06})`}}>
                            <header className="major">
                                <h3>Attend a Service</h3>
                                <p>what to expect	</p>
                            </header>
                            <Link to="/attend" className="link primary"></Link>
                        </article>
                    </section>
                    <section id="two">
                        <div className="inner">
                            <header className="major">
                                <h2>Welcome Home!</h2>
                            </header>
                            <p>We hope when you walk through the doors you will immediately recognize that you are among friends: a diverse, wonderful group of people who love Jesus and who are committed to helping you discover yourself in the light of the Word of God. We have been praying for you, and no matter your background or life experience, you will find a place where you are warmly welcomed and feel right at home!
<br/>Every weekend we gather for worship and teaching on <strong>Sundays at 10:25am</strong>. <br/>There are services and classes for your kids during our worship experience on the weekend and <strong>Wednesday evenings at 7:00pm</strong>.</p>
                            <ul className="actions">
                                <li><Link to="/newhere" className="button next">I'm New</Link></li>
                            </ul>
                        </div>
                    </section>
                </div>

            </div>
        )
    }
}

export default HomeIndex

export const pageQuery = graphql`
    query PageQuery {
        site {
            siteMetadata {
                title
                description
            }
        }
    }
`


